import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForSoftSkill } from '../../mock/aboutMe/softSkillsData';
import '../../style/pages/_skills.scss';

const generateUniqueKey = () => {
  return Math.random().toString(36).substring(7);
};

const SoftSkills = ({ softSkills }) => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container fluid className="softskill-page-container">
      <Row className={`softskill-row ${isMobile ? 'mobile-layout' : ''}`}>
        {Object.keys(softSkills).map((category) => (
          <Col lg={4} sm={12} key={category} className="softskill-column">
            <div className="softskill-card">
              <h2 className="softskill-category-heading">{category}</h2>
              {softSkills[category].map((softskill) => (
                <div className="softskill-item" key={generateUniqueKey()}>
                  <FontAwesomeIcon
                    icon={getIconForSoftSkill(softskill)}
                    className="softskill-icon"
                  />
                  <span className="softskill-text">{softskill}</span>
                </div>
              ))}
            </div>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

SoftSkills.propTypes = {
  softSkills: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default SoftSkills;
